.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: rgb(118, 126, 126);
  }
  
  .register-box {
    padding: 30px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
    width: 400px;
  }
  
  .register-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .register-header h1 {
    font-size: 24px;
    margin-bottom: 5px;
  }
  
  .register-header p {
    color: #888;
  }
  
  .register-footer {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .register-footer button {
    padding: 8px 16px;
  }
  
  .register-footer a {
    color: #888;
  }
  